import axios from "axios";

export const confiFiscal = async (method: string, url: string) => {
  try {
    var config = {
      method: method,
      url: url,
      headers: {
        "auth-token": localStorage.getItem("token"),
      },
    };

    const res = await axios(config);
    // console.log(res.data);
    return res;
  } catch (error) {
    // console.log(error);
    return error;
  }
};
