import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, {
    useMemo,
    useState,
    useCallback,
    Component,
    useEffect,
} from "react";

import { API } from "../axios/swr/endpoint";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import _ from "lodash";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { useNavigate, useLocation } from "react-router-dom";
import Card from "react-bootstrap/Card";
import { ChartOptions } from "chart.js";
import { Bar } from "react-chartjs-2";
import Container from "react-bootstrap/Container";
import { confiFiscal } from "../axios/configFiscal";

import {
    Chart as ChartJS,
    LineElement,
    CategoryScale, // x axis
    LinearScale, // y axis
    PointElement,
    BarElement,
    ArcElement,
    Tooltip,
    Title,
    Legend,
} from "chart.js";

ChartJS.register(
    LineElement,
    CategoryScale,
    LinearScale,
    PointElement,
    BarElement,
    ArcElement,
    Tooltip,
    Title,
    Legend
);

const ButtonPlush = styled.div`
  display: flex;
  justify-content: start;
  padding-top: 1%;
`;
const Flexrow = styled.div`
`;

const GraphBudget: React.FunctionComponent = () => {
    const { state } = useLocation();

    const [fiscalYearId, setfiscalYearId] = useState<any>();
    const [subbudgetID, setSubbudgetID] = useState<any>([]);
    const [sub_subbudgetID, setSub_subbudgetID] = useState<any>([]);

    const [fiscalYear, setFiscalYear] = useState<any>();

    const [reset, setReset] = useState<boolean>(false);
    const navigate = useNavigate();

    const [subbudgetId, setSubbudgetId] = useState<any>();
    const [dataSubbudgetName, setDataSubbudgetName] = useState<any>([]);
    const [dataSubbudgetBudget, setDataSubbudgetBudget] = useState<any>([]);
    const [dataSubbudgetUse, setDataSubbudgetUse] = useState<any>([]);

    const [budget, setBudget] = useState<any>([]);
    const [name, setName] = useState<any>([]);
    const [id, setId] = useState<any>([]);


    const [budgetUsed, setBudgetUsed] = useState<any>([]);
    const [nameUsed, setNameUsed] = useState<any>([]);
    const [idUsed, setIdUsed] = useState<any>([]);

    useEffect(() => {
        if (fiscalYearId) {

            let idBudgetgroup: any = []
            let nameBudgetgroup: any = []
            let budgetBudgetgroup: any = []

            let idSubUsed: any = []
            let nameSubUsed: any = []
            let budgetSubUsed: any = []

            console.log("fiscalYearId",fiscalYearId);
            {_.map(fiscalYearId, (items, index) => {
                budgetBudgetgroup.push(items?.budget)
                nameBudgetgroup.push(items?.name)
                idBudgetgroup.push(items?.budgetgroup_id)

            })}

            setBudget(budgetBudgetgroup)
            setName(nameBudgetgroup)
            setId(idBudgetgroup)

            console.log("subbudgetID",subbudgetID);
            for(let i = 0; i < id.length; i++){
                // console.log(id[i]);
                {_.map(subbudgetID, (item, idex) => {
                    if(id[i] === item?.budgetgroup_id){
                        idSubUsed.push(item?.budget)
                        nameSubUsed.push(item?.name)
                        budgetSubUsed.push(item?.use)
                    }
                })}
            }
       
            setBudgetUsed(budgetSubUsed)
            setNameUsed(nameSubUsed)
            setIdUsed(idSubUsed)

            console.log(idUsed);
            console.log(nameUsed);
            console.log(budgetUsed);
            
        }
    }, [fiscalYearId]);

    useMemo(async () => {
        const getData: any = await configAxios(
            "get",
            API.getbudgetgroupId + state.item.fiscalyear_id
        );

        const getDataSubbudget: any = await configAxios(
            "get",
            API.getsubbudget
        );
        if (getDataSubbudget.status === 200) {
            setSubbudgetID(getDataSubbudget.data);
        }

        const getDataSub_subbudget: any = await configAxios(
            "get",
            API.getSub_subbudget
        );
        if (getDataSub_subbudget.status === 200) {
            setSub_subbudgetID(getDataSub_subbudget.data)
        }


        if (getData.status === 200) {
            setfiscalYearId(getData.data);
        }

    }, [reset]);

    const data = {
        labels: name,
        datasets: [
            
            {
                label: "งบอนุมัติ",
                data: budget,
                backgroundColor: "#2227b6",
                borderColor: ["#f8f8f8"],
                borderWidth: 1,
            },
            {
                label: "ยอดที่ใช้ไป",
                data: budgetUsed,
                backgroundColor: "#990000",
                borderColor: ["#f8f8f8"],
                borderWidth: 1,
            }
        ]
    };

    const options: ChartOptions = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                // text: state.items.name
                // text: name
            }
        }

    };

    return (
        <>
            <Sidebar />
            <div>
                <Button
                    onClick={() => {
                        navigate(`/budget`, {});

                    }}
                    variant="link"
                    style={{
                        marginLeft: "4%",
                        color: "#ac2d07",
                    }}
                    className="btn  "
                >
                    สรุปผลงบประมาณ /
                </Button>
                <Button
                    onClick={() => {
                        const Toast = Swal.mixin({
                            toast: true,
                            position: "top-end",
                            showConfirmButton: false,
                            timer: 3000,
                            timerProgressBar: true,
                            didOpen: (toast) => {
                                toast.addEventListener("mouseenter", Swal.stopTimer);
                                toast.addEventListener("mouseleave", Swal.resumeTimer);
                            },
                        });

                        Toast.fire({
                            icon: "success",
                            title: "คุณกำลังอยู่ในหน้านี้",
                        });
                    }}
                    variant="link"
                    style={{
                        height: "50px",
                        color: "#ac2d07",
                    }}
                    className="btn  "
                >
                    กราฟสรุปงบประมาณ
                </Button>
            </div>

            <Container>
                <Card
                    style={{
                        backgroundColor: "#ffffff",
                        borderRadius: "20px",
                        color: "#F16F22",
                        fontWeight: "bold",
                        borderColor: " #BF360C",
                        borderWidth: "3px",
                    }}
                    className="m-4 p-3"
                >
                    <Bar data={data} options={options}></Bar>
                </Card>
            </Container>
        </>
    );
};

export default GraphBudget;