import axios from "axios";

export const configAxios = async (
  method: string,
  url: string,
  data?: object
) => {
  try {
    var config = {
      method: method,
      url: url,
      headers: {
        "auth-token": localStorage.getItem("token"),
      },
      data: data,
    };

    const res = await axios(config);
    // console.log(res.data);
    return res;
  } catch (error) {
    // console.log(error);
    return error;
  }
};
