import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, { useMemo, useState } from "react";
import axios from "axios";
import { API } from "../axios/swr/endpoint";
import { configAxios } from "../axios/configAxios";
import _ from "lodash";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { baseURL } from "../axios/configBaseBRL";
import { useNavigate, useLocation } from "react-router-dom";
import { Container } from "react-bootstrap";

const Adminall = styled.div``;
const Useredit: React.FunctionComponent = () => {
  const [show, setShow] = useState(false);
  const [showEdit, setshowEdit] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleShowEdit = () => setshowEdit(true);
  const handleCloseEdit = () => setshowEdit(false);
  const navigate = useNavigate();

  const [admin, setAdmin] = useState<any>();
  const [name, setName] = useState<any>();
  const [lastName, setLastName] = useState<any>();
  const [phoneNumber, setPhoneNumber] = useState<any>();
  const [userName, setUserName] = useState<any>();
  const [password, setPassword] = useState<any>();
  const [reset, setReset] = useState<boolean>(false);
  const { state } = useLocation();
  useMemo(async () => {
    const getData: any = await configAxios(
      "get",
      state.item.admin_id + API.getadmin
    );
    if (getData.status === 200) {
      setAdmin(getData.data);
    } else {
    }
  }, [reset]);

  const saveButton = async () => {
    var data = {
      fname: name,
      lname: lastName,
      phone_number: phoneNumber,
     
    };
    var config = {
      method: "put",
      url: `${baseURL}/update/admin/${state.item.admin_id}`,
      headers: {
        "auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      data: data,
    };
    try {
      const addadmin = await axios(config);
      setShow(false);
      setshowEdit(false);
      setName("");
      setLastName("");
      setPhoneNumber("");
      setUserName("");
      setReset(!reset);
    } catch (error: any) {
      // console.log(error.response.status);
    }
  };
  return (
    <>
      <Sidebar />
      <div>
      <Button
        onClick={() => {
          navigate(`/admin`, {});
        }}
        variant="link"
        style={{
          marginLeft: "4%",
            // height: "50px",
            color: "#ac2d07",
        }}
        className="btn  "
      >
        จัดการสิทธิ์ผู้ดูแลระบบ /
      </Button>
      <Button
        onClick={() => {
          Swal.fire({
            position: "top-end",
            title: "คุณกำลังอยู่ในหน้านี้",
            showConfirmButton: false,
            timer: 1500,
          });
        }}
        variant="link"
        style={{
            color: "#ac2d07",
        }}
        className="btn  "
      >
        แก้ไขข้อมูลผู้ดูแลระบบ
      </Button>
      </div>
      <React.Fragment>
        <Container className="content">
          <div className="row">
            <div className="col-sm-12">
              <h2 className="mt-4 mb-4 mx-5 fw-bold">
                แก้ไขข้อมูล Username : {state.item.username}
              </h2>
              <form className="row g-3 mx-5">
                <div className="col-md-3">
                  <label className="form-label">Name</label>
                  <input
                    type="text"
                    name="name"
                    className="form-control p-2"
                    // placeholder={state.item.fname}
                    placeholder={state.item.fname}
                    value={name}
                    // value={state.item.fname}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setName(value);
                    }}
                  />
                </div>
                <div className="col-md-3">
                  <label className="form-label">Last name</label>
                  <input
                    type="text"
                    name="lastName"
                    className="form-control p-2"
                    placeholder={state.item.lname}
                    value={lastName}
                    // value={state.item.lname}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setLastName(value);
                    }}
                  />
                </div>
                <div className="col-md-3">
                  <label className="form-label">Phonenumber</label>
                  <input
                    type="text"
                    name="phonNumber"
                    className="form-control p-2"
                    value={phoneNumber}
                    placeholder={state.item.phone_number}
                    // value={state.item.phone_number}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setPhoneNumber(value);
                    }}
                  />
                </div>
              </form>
            </div>
          </div>
        </Container>

        <Container className="content">
          <div className="row">
            <div className="col-sm-12">
              <form className="row g-3 mx-5">
                <div className="col-md-3">
                  
                  <button
                    onClick={async () => {
                      // removeButton(item?.budgetgroup_id);
                      // setReset(!reset);
                      Swal.fire({
                        title:
                          "แก้ไขข้อมูล Usernamer  " + state.item.username + "?",
                        text: "หากแก้ไขแล้วคุณจะเปลี่ยนกลับไม่ได้!",
                        icon: "warning",
                        showDenyButton: true,
                        confirmButtonText: "ยืนยัน",
                        denyButtonText: "ยกเลิก",
                        confirmButtonColor: "#0f754e",
                      }).then(async(result) => {
                        if (result.isConfirmed) {
                          Swal.fire({
                            position: "center",
                            icon: "success",
                            title: "ข้อมูลของคุณถูกแก้ไขสำเสร็จ",
                            showConfirmButton: false,
                            timer: 1500,
                          });
                          {
                            await saveButton();

                            navigate(-1);
                            // removeButton(item?.budgetgroup_id);
                            // setReset(!reset);
                          }
                        } else if (result.isDenied) {
                          // Swal.fire("Changes are not saved", "", "info");
                        }
                      });
                    }}
                    type="button"
                    className="btn py-1 btn-primary mt-4"
                    // type="submit" className="btn btn-primary mt-4"
                  >
                    {" "}
                    ยืนยัน
                    {/* <i className="fa-solid fa-trash"></i> */}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </Container>
      </React.Fragment>
    </>
  );
};

export default Useredit;
