import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, {
  useMemo,
  useState,
  useCallback,
  Component,
  useEffect,
} from "react";

import axios from "axios";
import { API } from "../axios/swr/endpoint";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import _ from "lodash";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { useNavigate, useLocation } from "react-router-dom";
import { confiFiscal } from "../axios/configFiscal";
import { baseURL } from "../axios/configBaseBRL";
import { Grid, GridColumn, GridToolbar } from "@progress/kendo-react-grid";
import { ExcelExport } from "@progress/kendo-react-excel-export";
import { FaBox } from "react-icons/fa";
// import Box from "@mui/material/Box";
import { View, StyleSheet, Text } from "react-native";
import Card from "react-bootstrap/Card";
import { ChartOptions } from "chart.js";
import ExportExcel from "../components/ExportExcel";
import colorChart from "../components/color";
import { Pie } from "react-chartjs-2";
import { Bar } from "react-chartjs-2";

import {
  Chart as ChartJS,
  LineElement,
  CategoryScale, // x axis
  LinearScale, // y axis
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend,
} from "chart.js";
import { utils } from "sheetjs-style/types";


ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend
);

const SummaryGraph: React.FunctionComponent = () => {


  const { state } = useLocation();

  const [reset, setReset] = useState<boolean>(false);
  const [subbudgetId, setSubbudgetId] = useState<any>();
  const [subbudgetID, setSubbudgetID] = useState<any>([]);

  const [dataSubbudgetName, setDataSubbudgetName] = useState<any>([]);
  const [dataSubbudgetBudget, setDataSubbudgetBudget] = useState<any>([]);

  const [dataSubbudgetUse, setDataSubbudgetUse] = useState<any>([]);
  const navigate = useNavigate();
  useEffect(() => {
    if (subbudgetId) {

      // console.log("subbudgetId", subbudgetId)

      let dataSubbudgetName: any = []
      let dataSubbudgetBudget: any = []
      let dataSubbudgetUse: any = []
      for (let i = 0; i < subbudgetId.length; i++) {
        dataSubbudgetName.push(subbudgetId[i].name)
        dataSubbudgetBudget.push(subbudgetId[i].budget)
        dataSubbudgetUse.push((subbudgetId[i].use))
      }

      // console.log("ad",dataSubbudgetName)
      // console.log("pp",dataSubbudgetBudget)
      setDataSubbudgetName(dataSubbudgetName)
      setDataSubbudgetBudget(dataSubbudgetBudget)
      setDataSubbudgetUse(dataSubbudgetUse)

      console.log("dataSubbudgetName", dataSubbudgetName);
      console.log("dataSubbudgetBudget", dataSubbudgetBudget);
      console.log("dataSubbudgetUse", dataSubbudgetUse);



    }
  }, [subbudgetId]);

  useMemo(async () => {
    const getData: any = await configAxios(
      "get",
      API.getsubbudget + state.items.superbudget_id
    );

    const getDataSubbudget: any = await configAxios(
      "get",
      API.getsubbudget
    );

    if (getData.status === 200) {
      // console.log("OK")
      setSubbudgetId(getData.data);
    }

    if (getDataSubbudget.status === 200) {
      // console.log("OKKKKKK")
      setSubbudgetID(getDataSubbudget.data);
    }


  }, [reset]);


  const data = {
    labels: dataSubbudgetName,
    datasets: [
      {
        label: "งบอนุมัติ",
        data: dataSubbudgetBudget,
        // backgroundColor: getRandomColor,
        backgroundColor: "#731D26",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      },
      {
        label: "ใช้จริง",
        data: dataSubbudgetUse,
        // backgroundColor: getRandomColor,
        backgroundColor: "#CD6C07",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      }
    ]
  };

  const options: ChartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',

      },
      title: {
        display: true,
        text: state.items.name
      }
    }
  };

  return (
    <>
      <Sidebar />

      <div>
        <Button
          onClick={() => {
            navigate(`/Summary`, {});
            // console.log("on click สรุปผลงบประมาณ");

          }}
          variant="link"
          style={{
            marginLeft: "4%",
            // height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          สรุปการใช้จริง /
        </Button>
        <Button
          onClick={() => {
            navigate(-1);
          }}
          variant="link"
          style={{
            // height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          รายละเอียดการใช้จริง /
        </Button>
        <Button
          onClick={() => {
            // console.log("on click รายละเอียดปีงบประมาณ");
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          {state.items.name}
          {/* {state.item.name} */}
        </Button>
      </div>

      <Card
        style={{
          backgroundColor: "#ffffff",
          borderRadius: "20px",
          color: "#F16F22",
          fontWeight: "bold",
          borderColor: " #BF360C",
          borderWidth: "3px",
        }}
        className="m-4 p-3"
      >
        <Bar data={data} options={options}></Bar>

      </Card>

    </>
  );

};

export default SummaryGraph;