import React from 'react'
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import Sidebar from './components/Sidebar'
import Login from './pages/Login';
// import Home from './pages/BudgetMN';
// import Team from './pages/Budget';
// import Tasks from './pages/Admin';
// import Chats from './pages/Chats';
// import Analytics from './pages/Analytics';
import BudgetMN from './pages/BudgetMN';
import Budget from './pages/Budget';
import Admin from './pages/Admin';
import Editbudget from './pages/Editbudget';
import ButgetInYear from './pages/BudgetInYear';
import Totalbudget   from './pages/Totalbudget';
import Useredit   from './pages/useredit';
import Budgetedit   from './pages/budgetedit';
import Tracking from './pages/Tracking';
import Summary from './pages/Summary';
import TrackingInYear from './pages/TrackingInYear';
import SummaryInYear from './pages/SummaryInYear';
import SummaryGraph from './pages/SummaryGraph';
import GraphBudget from './pages/GraphBudget';



const App: React.FunctionComponent = () => {
  return (

    
      <Router>
        {/* <Login/>
        <Route path='/' element={<Login />} /> */}
       
   
        <Routes>
        <Route path='/' element={<Login />} />
          <Route path='/BudgetMN' element={<BudgetMN/>} />
          <Route path='/Budget' element={<Budget/>} />            
          <Route path='/Admin' element={<Admin/>} /> 
          <Route path='/Editbudget/:fiscalyear_id' element={<Editbudget/>} />   
          <Route path='/ButgetInYear/:fiscalyear_id' element={<ButgetInYear/>} /> 
          <Route path='/Totalbudget/:budgetgroup_id' element={<Totalbudget/>} /> 
          <Route path='/useredit/:admin_id' element={<Useredit/>} /> 
          <Route path='/budgetedit/:superbudget_id' element={<Budgetedit/>} />
          <Route path='/Tracking' element={<Tracking/>} /> 
          <Route path='/Summary' element={<Summary/>} />  
          <Route path='/TrackingInYear/:fiscalyear_id' element={<TrackingInYear/>} />  
          <Route path='/SummaryInYear/:budgetgroup_id' element={<SummaryInYear/>}/>
          <Route path='/SummaryGraph/:superbudget_id' element={<SummaryGraph/>}/>
          <Route path='/GraphBudget/:fiscalyear_id' element={<GraphBudget/>}/>
          
          {/* Totalbudget   */}
          {/* <Route path='/ButgetInYear' element={<ButgetInYear/>} />   */}
          {/* <Route path='/chats' element={<Chats />} />
          <Route path='/analytics' element={<Analytics />} /> */}
        </Routes>
      </Router> 
 
  )
}

export default App
