import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, { useMemo, useState } from "react";
import axios from "axios";
import { API } from "../axios/swr/endpoint";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import _, { rest } from "lodash";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { baseURL } from "../axios/configBaseBRL";
import { EditText, EditTextarea } from "react-edit-text";
import { useNavigate } from "react-router-dom";
import Container from "react-bootstrap/Container";
const TaskText = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  height: 70vh;
`;
const TableHeader = styled.div`
  margin-left: 6rem;
  margin-right: 6rem;
  /* margin-bottom: 6rem; */
  margin-top: 0rem;
  text-align: center;
`;
const Flexmodal = styled.div`
  display: flex;
  font-weight: bold;
`;

const ButtonPlush = styled.div`
  display: flex;
  justify-content: start;
  padding-top: 1%;
`;
const Adminall = styled.div``;
const Admin: React.FunctionComponent = () => {
  const [show, setShow] = useState(false);
  const [showEdit, setshowEdit] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleShowEdit = () => setshowEdit(true);
  const handleCloseEdit = () => setshowEdit(false);
  const navigate = useNavigate();

  const [admin, setAdmin] = useState<any>();

  const [adminId, setAdminId] = useState<any>();
  const [name, setName] = useState<any>();
  const [lastName, setLastName] = useState<any>();
  const [phoneNumber, setPhoneNumber] = useState<any>();
  const [userName, setUserName] = useState<any>();
  const [password, setPassword] = useState<any>();
  const [reset, setReset] = useState<boolean>(false);

  const [showEditAdmin, setShowEditAdmin] = useState(false);
  const handleCloseEditAdmin = () => setShowEditAdmin(false);
  const handleShowEditAdmin = () => setShowEditAdmin(true);
  const [query, setQuery] = useState("");

  useMemo(async () => {
    try {
      const getData: any = await configAxios("get", API.getadmin);
      setAdmin(getData.data);
    } catch (error) {
      // console.log(error);
    }
  }, [reset]);

  const saveButton = async () => {
    var data = {
      fname: name,
      lname: lastName,
      phone_number: phoneNumber,
      username: userName,
      password: password,
    };
    var config = {
      method: "post",
      url: `${baseURL}/create/admin?`,
      headers: {
        "auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      data: data,
    };

    try {
      const addadmin = await axios(config);
      setShow(false);
      setshowEdit(false);
      setName("");
      setLastName("");
      setPhoneNumber("");
      setUserName("");
      setPassword("");
      setReset(!reset);
    } catch (error: any) {
      // console.log(error.response.status);
    }
  };
  const saveEditAdmin = async () => {
    var data = {
      fname: name,
      lname: lastName,
      phone_number: phoneNumber,
    };
    var config = {
      method: "put",
      url: `${baseURL}/update/admin/${adminId}`,
      headers: {
        "auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      data: data,
    };
    try {
      const addadmin = await axios(config);
      setShowEditAdmin(false);
      // setshowEdit(false);
      setName("");
      setLastName("");
      setPhoneNumber("");
      setUserName("");
      setReset(!reset);
    } catch (error: any) {
      // console.log(error.response.status);
    }
  };

  const removeButton = async (id: number) => {
    var data = JSON.stringify({
      admin_id: id,
    });

    var config = {
      method: "post",
      url: `${baseURL}/delete/admin`,
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };

    try {
      const mss = await axios(config);
      // console.log(mss.data);
      setReset(!reset);
    } catch (error: any) {
      // console.log(error);
    }
  };

  return (
    <>
      <Sidebar />

      <Adminall>
        <ButtonPlush>
          <button
            onClick={handleShow}
            type="button"
            style={{
              marginLeft: "6%",
              borderRadius: "12px",
              height: "50px",
              fontSize: "15px",
              alignItems: "center",
              backgroundColor: "#BF360C",
              borderColor: " #BF360C",
              borderWidth: "3px",
              color: "#ffffff",
              fontWeight: "bold",
            }}
            className="btn btn "
          >
            เพิ่มผู้ดูแลระบบ <i className="fa-solid fa-plus"></i>
          </button>
        </ButtonPlush>
        {/* <input
          className="search"
          placeholder="Search..."
          onChange={(e) => setQuery(e.target.value.toLowerCase())}
        /> */}
        <Container className="my-3">
          {/* <div className="input-group" style={{display: "flex", justifyContent: "center" ,}}>
            <div className="form-outline" >
              <input type="search" id="form1" className="form-control"  onChange={(e) => setQuery(e.target.value.toLowerCase())}/>
              <label className="form-label" form="form1">
                Search.....
              </label>
            </div>
            <button type="button" className="btn btn-primary">
              <i className="fas fa-search"></i>
            </button>
          </div> */}
          <Table
            // style={{ color: "#504c4c" }}
            //   striped
            bordered
            //   hover
            size="lg"
          >
            <thead
              style={{
                backgroundColor: "#BF360C ",
                color: "#ffffff",
                fontWeight: "bold",
              }}
            >
              <tr style={{ textAlign: "center" }}>
                <th>ชื่อผู้ใช้</th>
                <th>ชื่อ</th>
                <th>นามสกุล</th>
                <th>เบอร์โทรศัพท์</th>
                <th>แก้ไข</th>
                {/* <th>Remove</th> */}
              </tr>
            </thead>
            <tbody>
              {admin != undefined && (
                <>
                  {/* <tr className="list"> */}
                  {admin
                    .filter((item: any) =>
                      item.username.toLowerCase().includes(query)
                    )
                    .map((item: any) => (
                      <tr
                        className="listItem "
                        key={item.admin_id}
                        style={{ textAlign: "center" }}
                      >
                        <td>{item.username}</td>
                        <td>{item.fname}</td>
                        <td>{item.lname}</td>
                        <td>{item.phone_number}</td>
                        <td>
                          <button
                            onClick={() => {
                              setAdminId(item?.admin_id);
                              setName(item?.fname);
                              setLastName(item?.lname);
                              setPhoneNumber(item?.phone_number);
                              setShowEditAdmin(true);
                            }}
                            style={{
                              backgroundColor: "#FFB800",
                              color: "#000000",
                            }}
                            type="button"
                            className="btn "
                          >
                            <i className="fa-solid fa-pen-to-square"></i>
                          </button>
                        </td>
                      </tr>
                    ))}
                  {/* </tr> */}
                </>
              )}
            </tbody>
            <tbody>
              <tr></tr>
            </tbody>
          </Table>
        </Container>

        <Modal
          show={show}
          onHide={handleClose}
          // size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Body>
            <Modal.Header closeButton></Modal.Header>
            <Modal.Title
              style={{
                alignItems: "center",
                color: "#484848",
                textAlign: "center",
                padding: "10px 10px 30px 10px",
                fontWeight: "bold",
              }}
            >
              เพิ่มผู้ดูแลระบบ
            </Modal.Title>
            <div className="d-flex justify-content-center">
              <Form>
                <Form.Group
                  className="mb-3"
                  controlId="exampleForm.ControlInput1"
                >
                  <Form.Control
                    style={{
                      backgroundColor: "#ffffff",
                      borderColor: " #000000",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                    }}
                    type="text"
                    placeholder="ชื่อ"
                    name="name"
                    value={name}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setName(value);
                    }}
                  />
                  <Form.Control
                    style={{
                      backgroundColor: "#ffffff",
                      borderColor: " #000000",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                      margin: "1.5rem auto",
                    }}
                    type="text"
                    placeholder="นามสกุล"
                    name="lastName"
                    value={lastName}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setLastName(value);
                    }}
                    // autoFocus
                  />
                  <Form.Control
                    style={{
                      backgroundColor: "#ffffff",
                      borderColor: " #000000",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                      // margin: "1.5rem auto",
                    }}
                    type="text"
                    placeholder="เบอร์โทรศัพท์"
                    name="phonNumber"
                    value={phoneNumber}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setPhoneNumber(value);
                    }}
                    // autoFocus
                  />
                  <Form.Control
                    style={{
                      backgroundColor: "#ffffff",
                      borderColor: " #000000",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                      margin: "1.5rem auto",
                    }}
                    type="text"
                    placeholder="ชื่อผู้ใช้"
                    name="userName"
                    value={userName}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setUserName(value);
                    }}
                    // autoFocus
                  />
                  <Form.Control
                    style={{
                      backgroundColor: "#ffffff",
                      borderColor: " #000000",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                      margin: "1.5rem auto",
                    }}
                    type="text"
                    placeholder="รหัสผ่าน"
                    name="password"
                    value={password}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setPassword(value);
                    }}
                    // autoFocus
                  />
                </Form.Group>
              </Form>
            </div>
          </Modal.Body>

          <Button
            style={{
              display: "flex",
              justifyContent: "center",
              color: "#ffffff",
              backgroundColor:
                name && lastName && phoneNumber && userName && password
                  ? "#278A43"
                  : "#6b6b6b",
              border: "none",
            }}
            onClick={() => {
              if (name && lastName && phoneNumber && userName && password) {
                saveButton();
              }
            }}
          >
            บันทึกข้อมูล
          </Button>
        </Modal>

        <Modal
          show={showEditAdmin}
          onHide={handleCloseEditAdmin}
          // size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Body 
            style={{
              textAlign: "center",
            }}
          >
            <Modal.Header closeButton></Modal.Header>
            <Modal.Title
              style={{
                alignItems: "center",
                color: "#484848",
                textAlign: "center",
                padding: "10px ",
                fontWeight: "bold",
              }}
            >
              แก้ไขข้อมูลผู้ดูแลระบบ
            </Modal.Title>
            <div >
              <Form>
                <Form.Group
                  className="mb-3"
                  controlId="exampleForm.ControlInput1"
                >
                  <Form.Label
                  style={{
                    float: "left",
                  }}
                >
                  ชื่อ
                </Form.Label>
                  <Form.Control
                     style={{
                      borderColor: " #F16F22",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                    }}
                    type="text"
                    placeholder="ชื่อ"
                    name="name"
                    value={name}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setName(value);
                    }}
                  />
                   </Form.Group>
                   <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                  <Form.Label
                  style={{
                    float: "left",
                  }}
                >
                  นามสกุล
                </Form.Label>
                  <Form.Control
                     style={{
                      borderColor: " #F16F22",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                    }}
                    type="text"
                    placeholder="นามสกุล"
                    name="lastName"
                    value={lastName}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setLastName(value);
                    }}
                    // autoFocus
                  />
                   </Form.Group>
                   <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                  <Form.Label
                  style={{
                    float: "left",
                  }}
                >
                  เบอร์โทรศัพท์
                </Form.Label>
                  <Form.Control
                     style={{
                      borderColor: " #F16F22",
                      borderWidth: "2px",
                      borderRadius: "12px",
                      color: "#000000",
                      fontWeight: "bold",
                    }}
                    type="text"
                    placeholder="เบอร์โทรศัพท์"
                    name="phonNumber"
                    value={phoneNumber}
                    onChange={(event: any) => {
                      const value = event.target.value;
                      setPhoneNumber(value);
                    }}
                    // autoFocus
                  />
                </Form.Group>
              </Form>
            </div>
          </Modal.Body>

          <button
            onClick={async () => {
              
              // removeButton(item?.budgetgroup_id);
              // setReset(!reset);
              Swal.fire({
                title: "คุณต้องการแก้ไขข้อมูล ",
                text: "หากแก้ไขแล้วคุณจะเปลี่ยนกลับไม่ได้!",
                icon: "warning",
                showDenyButton: true,
                confirmButtonText: "ยืนยัน",
                denyButtonText: "ยกเลิก",
                confirmButtonColor: "#0f754e",
              }).then(async (result) => {
                if (result.isConfirmed) {
                  Swal.fire({
                    position: "center",
                    icon: "success",
                    title: "ข้อมูลของคุณถูกแก้ไขสำเสร็จ",
                    showConfirmButton: false,
                    timer: 1500,
                  });
                  {
                    await saveEditAdmin();

                    // navigate(-1);
                    // removeButton(item?.budgetgroup_id);
                    setReset(!reset);
                  }
                } else if (result.isDenied) {
                  // Swal.fire("Changes are not saved", "", "info");
                }
              });
            }}
            style={{
              backgroundColor: "#BF360C ",
              color: "#ffffff",
              fontWeight: "bold",
            }}
            type="button"
            className="btn py-1 "
            // type="submit" className="btn btn-primary mt-4"
          >
            {" "}
            ยืนยัน
            {/* <i className="fa-solid fa-trash"></i> */}
          </button>
        </Modal>
      </Adminall>
    </>
  );
};

export default Admin;
