import React, { useMemo, useState, useCallback } from "react";
import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import { useNavigate } from "react-router-dom";
import _, { set } from "lodash";
import { API } from "../axios/swr/endpoint";
import { confiFiscal } from "../axios/configFiscal";
import background from "../img/login_R.png";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";

import {
  MDBCard,
  MDBCardBody,
  MDBCardText,
  MDBCardImage,
} from "mdb-react-ui-kit";

const TeamText = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  height: 70vh;
  padding: center;
`;
const BudgetGrop = styled.div`
  /* display: column; */
`;

const Summary: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [fiscalYear, setFiscalYear] = useState<any>();
  //   const [budget, setBudget] = useState<string>();
  //   const [nameFac, setNameFac] = useState<string>();
  //   const [reset, setReset] = useState<boolean>(false);

  useMemo(async () => {
    const getData: any = await confiFiscal("get", API.getfiscalyear);
    // console.log(getData);
    if (getData.status == 200) {
      setFiscalYear(getData.data);
      //   setNameFac(getData.data.name);
    } else {
      // console.log(getData);
    }
  }, []);

  return (
    <>
      <Sidebar />
      <div>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            marginLeft: "4%",
            borderRadius: "12px",
            height: "50px",
            color: "#ac2d07",
            fontSize: "17px",
            alignItems: "center",
            
          }}
          className="btn  "
        >
          สรุปการใช้จริง
        </Button>

      </div>
      <Container>
        <BudgetGrop className="d-flex justify-content-center flex-row flex-wrap ">
          {_.map(fiscalYear, (item: any, idx) => {
            return (
              <>
                {/* <div className="card mb-3 mx-1 " style={{ width: "18rem ", height: "15rem" , border: "#351708" }}> */}

                <Button variant="link"
                  // href=""
                  className="btn btn-primary m-2 p-5"
                  onClick={() => {
                    // console.log("item", item);

                    navigate(`/SummaryInYear/${item?.fiscalyear_id}`, {
                      state: { item: item },

                    });


                    // if (item.budgetgroups === null || item.budgetgroups) {
                    

                    // }
                    

                  }}
                  style={{
                    backgroundColor: "#d8fffc",
                    // width: "20rem ",
                    height: "15rem",
                    borderRadius: "20px",
                    color: "#000000",
                    fontWeight: "bold",
                    borderColor: " #000000",
                    borderWidth: "3px",
                    textDecoration: "none",
                    // fontSize: "2px",
                  }}
                // className="m-5 p-5"
                >
                  {/* <h3 className="card-text mb-5">{item.name}</h3> */}
                  <p className="d-flex align-items-center card-text "></p>
                  <p className="card-text ">{item.name}</p>
                  <p className="card-text ">
                    งบประมาณทั้งหมด
                  </p>
                  <p
                    className="card-data--budget"
                  >
                    {item.budget.toLocaleString('en-US')} บาท
                  </p>
                </Button>
                {/* </div> */}
              </>
            );
          })}
        </BudgetGrop>
      </Container>
    </>
  );
};

export default Summary;
