import React, { useMemo, useState, useCallback } from "react";
import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import { useNavigate } from "react-router-dom";
import _, { set } from "lodash";
import { API } from "../axios/swr/endpoint";
import { confiFiscal } from "../axios/configFiscal";
import background from "../img/login_R.png";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";

import {
  MDBCard,
  MDBCardBody,
  MDBCardText,
  MDBCardImage,
} from "mdb-react-ui-kit";

const TeamText = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  height: 70vh;
  padding: center;
`;
const BudgetGrop = styled.div`
  /* display: column; */
`;

const Budget: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [fiscalYear, setFiscalYear] = useState<any>();

  useMemo(async () => {
    const getData: any = await confiFiscal("get", API.getfiscalyear);
    if (getData.status == 200) {
      setFiscalYear(getData.data);
    } else {

    }
  }, []);

  return (
    <>
      <Sidebar />
      <div>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            marginLeft: "4%",
            borderRadius: "12px",
            height: "50px",
            color: "#ac2d07",
            fontSize: "17px",
            alignItems: "center",
          }}
          className="btn  "
        >
          สรุปผลงบประมาณ
        </Button>

      </div>

      <Container>
        <BudgetGrop className="d-flex justify-content-center flex-row flex-wrap ">
          {_.map(fiscalYear, (item: any, idx) => {
            return (
              <>

                {/* <Button variant="link"
                  className="btn btn-primary m-2 p-5"
                  onClick={() => {
                    navigate(`/ButgetInYear/${item?.fiscalyear_id}`, {
                      state: { item: item },

                    });

                  }}
                  style={{
                    backgroundColor: "#ffefce",
                    height: "15rem",
                    borderRadius: "20px",
                    color: "#054A8A",
                    fontWeight: "bold",
                    borderColor: " #BF360C",
                    borderWidth: "3px",
                    textDecoration: "none",
                  }}
                >
                  <p className="d-flex align-items-center card-text "></p>
                  <p className="card-text ">{item.name}</p>
                  <p className="card-text ">
                    งบประมาณทั้งหมด
                  </p>
                  <p
                    className="card-data--budget"
                  >
                    {item.budget.toLocaleString('en-US')} บาท
                  </p>
                </Button> */}

                <Card
                  className="m-2 p-5"
                  style={{
                    backgroundColor: "#ffefce",
                    height: "20rem",
                    borderRadius: "20px",
                    color: "#054A8A",
                    fontWeight: "bold",
                    borderColor: " #BF360C",
                    borderWidth: "3px",
                    textDecoration: "none",
                  }}
                >
                  <p className="d-flex align-items-center card-text "></p>
                  <p className="card-text ">{item.name}</p>
                  <p className="card-text ">
                    งบประมาณทั้งหมด
                  </p>
                  <p
                    className="card-data--budget"
                  >
                    {item.budget.toLocaleString('en-US')} บาท
                  </p>
                  <Button type="button"
                    className="btn btn-danger m-2 p-1"
                    onClick={() => {
                      navigate(`/ButgetInYear/${item?.fiscalyear_id}`, {
                        state: { item: item },

                      });

                    }}
                  >
                    สรุปการจัดการงับประมาณ
                  </Button>
                  <Button type="button"
                    className="btn btn-danger m-2 p-1"
                    onClick={() => {
                      navigate(`/GraphBudget/${item?.fiscalyear_id}`, {
                        state: { item: item },

                      });

                    }}
                  >
                    กราฟสรุปงบประมาณ
                  </Button>
                </Card>
              </>
            );
          })}
        </BudgetGrop>
      </Container>
    </>
  );
};

export default Budget;
