// import React from "react";
import React, { useMemo, useState } from "react";
import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import Button from "react-bootstrap/Button";
import { useNavigate } from "react-router-dom";
import _ from "lodash";
import { API } from "../axios/swr/endpoint";
import { confiFiscal } from "../axios/configFiscal";
import Swal from "sweetalert2";
import Container from "react-bootstrap/Container";

const ButtonPlush = styled.div`
  display: flex;
  justify-content: start;
  padding-top: 1%;
`;
const Flexrow = styled.div``;
const BudgetGrop = styled.div``;

const Tracking: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [fiscalYear, setFiscalYear] = useState<any>();
  const [budget, setBudget] = useState<string>();
  const [nameFac, setNameFac] = useState<string>();
  const [reset, setReset] = useState<boolean>(false);

  useMemo(async () => {
    const getData: any = await confiFiscal("get", API.getfiscalyear);
    // console.log(getData);
    if (getData.status == 200) {
      setFiscalYear(getData.data);
      setNameFac(getData.data.name);
    } else {
      // console.log(getData);
    }
  }, [reset]);
  // console.log(fiscalYear);

  // console.log("fiscalYear,", fiscalYear);

  return (
    <>
      <Sidebar />

      <div>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            marginLeft: "4%",
            borderRadius: "12px",
            height: "50px",
            color: "#ac2d07",
            fontSize: "17px",
            alignItems: "center",
          }}
          className="btn  "
        >
          ติดตามการใช้งบประมาณ
        </Button>
      </div>
      <Container>
        <BudgetGrop className="d-flex justify-content-center flex-row flex-wrap  ">
          {_.map(fiscalYear, (item: any, idx) => {
            return (
              <>
                {/* <div className="card mb-3 mx-1 " style={{ width: "18rem ", height: "15rem" , border: "#351708" }}> */}
                <div className="card text-right m-2 " style={{ width: "18rem" }}>
                  <div className="card-body">
                    <h5 className="card-title">{item.name}</h5>
                    <p className="card-text">
                      {" "}
                      งบประมาณทั้งหมด {item.budget.toLocaleString("en-US")} บาท
                    </p>
                    <Button
                      // href=""
                      className="btn "
                      onClick={() => {
                        // console.log("item", item);

                        navigate(`/TrackingInYear/${item?.fiscalyear_id}`, {
                          state: { item: item },
                        });
                      }}
                      style={{
                        background: "#ac2d07",
                        color: "#ffffff",
                        border: "none"
                      }}
                    >
                      ติดตามการใช้งบประมาณ
                    </Button>
                  </div>
                </div>
              </>
            );
          })}
        </BudgetGrop>
      </Container>
    </>
  );
};

export default Tracking;
