import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, {
  useMemo,
  useState,
  useCallback,
  Component,
  useEffect,
} from "react";

import { API } from "../axios/swr/endpoint";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import _ from "lodash";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { useNavigate, useLocation } from "react-router-dom";
import Card from "react-bootstrap/Card";
import { ChartOptions } from "chart.js";
import ExportExcel from "../components/ExportExcel";
import { Pie } from "react-chartjs-2";
import { Bar } from "react-chartjs-2";

import {
  Chart as ChartJS,
  LineElement,
  CategoryScale, // x axis
  LinearScale, // y axis
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend,
} from "chart.js";

ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend
);

const Header = styled.div`
  display: flex;
  align-items: start;
  justify-content: start;
  font-size: 15px;
  /* height: 100px; */
  /* padding: center;
  text-align: center; */
  color: #dd631d;
  /* box-shadow: 1px 0px 8px 1px #615f5f; */
`;
const TableHeader = styled.div`
  /* display: flex;
  justify-content: center; */
  margin-left: 55px;
  margin-right: 55px;
  margin-bottom: 55px;
  background-color: #fff8e3;
  border-radius: 1px 0px 20px 20px;
  border-color: #680000;
  border-width: 20px;
  /* box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2), 0 2px 10px 0 rgba(0, 0, 0, 0.19); */
`;
const Flexrow = styled.div`
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 10px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  /* background-color: #fdefe7;
  border-color: #680000; */

  color: #054a8a;
  font-weight: bold;
`;
const commonStyles = {
  bgcolor: "#fdefe7",
  border: 5,
  borderradius: 25,
  m: 5,
  borderColor: "#680000",
  width: "50rem",
  height: "5rem",
};

const ButgetInYear: React.FunctionComponent = () => {

  const { state } = useLocation();
  const navigate = useNavigate();
  const [fiscalYearId, setfiscalYearId] = useState<any>();
  const [reset, setReset] = useState<boolean>(false);
  const axios = require("axios");

  const [remainInBudgetgroup, setRemainInBudgetgroup] = useState<number>(0);

  const [subbudgetID, setSubbudgetID] = useState<any>([]);

  const [budget, setBudget] = useState<any>([]);
  const [name, setName] = useState<any>([]);
  const [sum, setSum] = useState<number>(0);

  const [BUDGET, setBUDGET] = useState<any>([]);
  const [NAME, setNAME] = useState<any>([]);
  const [dataSubBudget, setDataSubBudget] = useState<any>([]);

  const [remain, setRemain] = useState<number>(0);
  const [sumInBudgetgroup, setSumInBudgetgroup] = useState<number>(0);
  const [excelExportData, setExcelExportData] = useState<any>([]);
  const [query, setQuery] = useState("");

  const [showChart, setShowChart] = useState<boolean>(false);

  useEffect(() => {

    if (fiscalYearId) {

      if (fiscalYearId) {
        setShowChart(true)
      } else {
        setShowChart(false)
      }


      const arr: any[] = [];
      let arrBudgetgroupName: any = []
      let arrBudgetgroupBudget: any = []

      let sumSub_sub = 0;
      let arrSumSub_sub: any = []
      let arrSub_subBudget: any = []

      for (let i = 0; i < fiscalYearId?.length; i++) {

        arrBudgetgroupName.push(fiscalYearId[i].name)
        arrBudgetgroupBudget.push(fiscalYearId[i].budget)

        arrSumSub_sub[i] = []

        let fiscalyearIdCount = fiscalYearId[i];

        if (fiscalyearIdCount?.superbudgets) {
          arr[i] = [];

          for (let j = 0; j < fiscalyearIdCount?.superbudgets?.length; j++) {
            arr[i][j] = fiscalyearIdCount?.superbudgets[j]?.budget;

            for (let k = 0; k < subbudgetID.length; k++) {
              if (subbudgetID[k]?.superbudget_id === fiscalyearIdCount?.superbudgets[j]?.superbudget_id) {
                sumSub_sub += subbudgetID[k].budget
              }

              arrSumSub_sub[i].push(sumSub_sub)
              sumSub_sub = 0
            }
          }
          let sumInBudgetgroup = arr[i].reduce((a: any, b: any) => a + b);
          setSumInBudgetgroup(sumInBudgetgroup);
        }

      }
      setNAME(arrBudgetgroupName)
      setBUDGET(arrBudgetgroupBudget)

      let total = 0
      for (let i = 0; i < arrSumSub_sub.length; i++) {

        for (let j = 0; j < arrSumSub_sub[i].length; j++) {
          total += arrSumSub_sub[i][j]
        }
        arrSub_subBudget.push(total)
        total = 0
      }
      setDataSubBudget(arrSub_subBudget)

      class dataFiscalyear {
        ลำดับ: any;
        ชื่อโครงการ: any;
        งบประมาณ: any;
        constructor(no: any, budgetgroup: any, budget: any) {
          this.ลำดับ = no;
          this.ชื่อโครงการ = budgetgroup;
          this.งบประมาณ = budget;
        }
      }


      let sumArr: any = [];
      for (let i = 0; i < arr.length; i++) {
        let budgetCount = arr[i];
        let sumBudget = 0;
        for (let j = 0; j < budgetCount?.length; j++) {
          let budgetCount1 = budgetCount[j];
          sumBudget += budgetCount1;
        }
        sumArr.push(sumBudget);
      }

      let sumBudgetInBudgetgroup = 0;
      const budgetInBudgetgroupArr = [];
      const nameInBudgetgroupArr = [];


      for (let count = 0; count < fiscalYearId.length; count++) {

        let budgetInBudgetgroup = fiscalYearId[count]?.budget;
        let nameInBudgetgroup = fiscalYearId[count]?.name;
        budgetInBudgetgroupArr.push(budgetInBudgetgroup);
        nameInBudgetgroupArr.push(nameInBudgetgroup);
        sumBudgetInBudgetgroup += budgetInBudgetgroup;

      }
      setSum(sumBudgetInBudgetgroup);

      let remainArr: any = [];
      for (let countRemain = 0; countRemain < budgetInBudgetgroupArr.length; countRemain++) {
        let remain = budgetInBudgetgroupArr[countRemain] - sumArr[countRemain];

        remainArr.push(remain);
      }

      setBudget(sumArr);

      let dataFiscalyearArr: any = [];
      let nameSuperbudget = [];
      let budgetSuperbudget = [];

      let no = 0;


      for (let countFiscalyear = 0; countFiscalyear < fiscalYearId.length; countFiscalyear++) {
        let data = new dataFiscalyear(
          (no += 1),
          fiscalYearId[0].name,
          fiscalYearId[0].budget
        );

        dataFiscalyearArr.push(data);
        let dataSuperbudgets = fiscalYearId[countFiscalyear].superbudgets;
        for (let countSuperbudget = 0; countSuperbudget < dataSuperbudgets.length; countSuperbudget++) {
          nameSuperbudget.push(dataSuperbudgets[countSuperbudget].name);
          budgetSuperbudget.push(dataSuperbudgets[countSuperbudget].budget);

          let data = new dataFiscalyear(
            "",
            dataSuperbudgets[countSuperbudget].name,
            dataSuperbudgets[countSuperbudget].budget
          );
          dataFiscalyearArr.push(data);
        }
        let data1 = new dataFiscalyear(
          "",
          "ยอดคงเหลือ",
          remainArr[countFiscalyear]
        );
        dataFiscalyearArr.push(data1);
      }
      nameInBudgetgroupArr.push("คงเหลือ");
      budgetInBudgetgroupArr.push(state.item.budget - sumBudgetInBudgetgroup);
      let remainBudgetgroup = state.item.budget - sumBudgetInBudgetgroup;
      setRemainInBudgetgroup(remainBudgetgroup);

      setName(nameInBudgetgroupArr);
      setBudget(budgetInBudgetgroupArr);

      setExcelExportData(dataFiscalyearArr);

    }
  }, [fiscalYearId, subbudgetID]);

  useMemo(async () => {
    const getData: any = await configAxios(
      "get",
      API.getbudgetgroupId + state.item.fiscalyear_id
    );

    const getDataSubbudget: any = await configAxios(
      "get",
      API.getsubbudget
    );

    // console.log(getData);
    if (getData.status === 200 && getDataSubbudget.status === 200) {
      setfiscalYearId(getData.data);
      setSubbudgetID(getDataSubbudget.data);
    } else {
    }
  }, [reset]);

  function getRandomColor() {
    var letters = "0123456789ABCDEF";
    var color = "#";
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  const labels = [
    "#fff3e0",
    "#ffe0b2",
    "#ffcc80",
    "#ffb74d",
    "#ffa726",
    "#fe8308",
    "#ff9800",
    "#fb8c00",
  ];

  const backgroundColorNew = [];
  for (let i = 0; i < labels.length; i++) {
    const r = Math.floor(Math.random() * 255);
    const g = Math.floor(Math.random() * 255);
    const b = Math.floor(Math.random() * 255);
    // console.log('rgba('+r+','+g+','+b+',0.2)');
    backgroundColorNew.push("rgba(" + r + "," + g + "," + b + ",0.6)");
  }
  //Pie-Chart//
  // const data = {
  //   labels: name,
  //   datasets: [
  //     {
  //       // label: "name",
  //       data: budget,
  //       // backgroundColor: getRandomColor,
  //       backgroundColor: backgroundColorNew,

  //       borderColor: ["#f8f8f8"],
  //       borderWidth: 1,
  //     },
  //   ],
  // };

  const options: ChartOptions = {
    // legend: {
    //   display: true,
    //   position: "bottom",
    //   align: "center"
    // }
  };

  const data = {
    labels: NAME,
    datasets: [

      {
        label: "งบอนุมัติ",
        data: BUDGET,
        backgroundColor: "#2227b6",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      },
      {
        label: "ยอดที่ใช้ไป",
        data: dataSubBudget,
        backgroundColor: "#990000",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      }
    ]
  };

  //
  return (
    <>
      <Sidebar />
      <div>
        <Button
          onClick={() => {
            navigate(`/budget`, {});

          }}
          variant="link"
          style={{
            marginLeft: "4%",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          สรุปผลงบประมาณ /
        </Button>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          รายละเอียดปีงบประมาณ
        </Button>
      </div>
      {/* Header */}
      <div className="m-4 " style={{ width: "15rem", textAlign: "right" }}>
        <div className="input-group rounded">
          <input
            type="search"
            className="form-control rounded"
            placeholder="ค้นหา"
            aria-label="Search"
            aria-describedby="search-addon"
            onChange={(e) => setQuery(e.target.value.toLowerCase())}
          />
          <span className="input-group-text border-1" id="search-addon">
            <i className="fas fa-search"></i>
          </span>
        </div>
      </div>

      <Card
        style={{
          backgroundColor: "#fffeef",
          borderRadius: "20px",
          color: "#F16F22",
          fontWeight: "bold",
          borderColor: " #BF360C",
          borderWidth: "3px",
        }}
        className="m-4 p-3"
      >
        {/* <>
          {fiscalYearId.length === undefined &&
            <> */}
        <div>
          <Table className="my-3">
            <thead>
              <tr style={{ color: "#F16F22" }}>
                <th
                  style={{
                    textAlign: "left",
                    fontWeight: "bold",
                    fontSize: "18px",
                  }}
                >
                  {state.item.name}
                </th>
                <th
                  style={{
                    textAlign: "right",
                    fontWeight: "bold",
                    fontSize: "18px",
                  }}
                >
                  {state.item.budget.toLocaleString("en-US")} บาท
                </th>
              </tr>
              {fiscalYearId != undefined && (
                <>
                  {fiscalYearId
                    .filter((item: any) =>
                      item.name.toLowerCase().includes(query)
                    )
                    .map((item: any, idx: any) => (
                      <tr style={{ color: "#054A8A" }}>
                        <th style={{ textAlign: "left", fontWeight: "bold" }}>
                          <Button variant="link"
                            onClick={() => {
                              navigate(`/Totalbudget/${item?.budgetgroup_id}`, {
                                state: { item: item },
                              });
                            }}
                          >
                            {idx + 1}.{"   "}
                            {item.name}
                          </Button>
                        </th>
                        <th style={{ textAlign: "right", fontWeight: "bold" }}>
                          <Button variant="link"
                            onClick={() => {
                              navigate(`/Totalbudget/${item?.budgetgroup_id}`, {
                                state: { item: item },
                              });
                            }}
                          >


                            {item.budget.toLocaleString("en-US")} บาท{" "}
                          </Button>
                        </th>
                      </tr>
                    ))}
                </>
              )}

              <tr style={{ color: "#F16F22", fontSize: "18px", }}>
                <th style={{ textAlign: "left", fontWeight: "bold" }}>
                  งบประมาณที่ใช้ไปทั้งหมด
                </th>
                <th style={{ textAlign: "right", fontWeight: "bold" }}>
                  {sum.toLocaleString("en-US")} บาท
                </th>
              </tr>
              <tr style={{ color: "#F16F22", fontSize: "18px", }}>
                <th style={{ textAlign: "left", fontWeight: "bold" }}>
                  ยอดคงเหลือ
                </th>
                <th style={{ textAlign: "right", fontWeight: "bold" }}>
                  {(state.item.budget - sum).toLocaleString("en-US")} บาท
                </th>
              </tr>
            </thead>
          </Table>

          <p
            style={{
              display: "flex",
              color: "#F16F22",
              fontWeight: "bold",
              border: "none",
            }}
          >
            กราฟสรุปการใช้เงินงบประมาณ
          </p>
          <Bar data={data} options={options}></Bar>

          {/* <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}
          >

            <div
              style={{
                width: "20rem",
              }}
            >
              <Pie data={data} options={options}></Pie>
            </div>

          </div> */}

        </div>

        {/* <div
          style={{
            display: "flex",
            justifyContent: "end",
            color: "#ac2d07",
          }}
        >
          <div className="my-4">
            <ExportExcel
              excelData={excelExportData}
              fileName={state.item.name}
            />
          </div>
        </div> */}

      </Card>

    </>
  );
};

export default ButgetInYear;
