import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import React, { useMemo, useState, useCallback, useEffect } from "react";
import axios from "axios";
import { API } from "../axios/swr/endpoint";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import _ from "lodash";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Swal from "sweetalert2";
import { useNavigate, useLocation } from "react-router-dom";
import { confiFiscal } from "../axios/configFiscal";
import { baseURL } from "../axios/configBaseBRL";

import ExportExcel from "../components/ExportExcel";

import Card from "react-bootstrap/Card";

import { ChartOptions } from "chart.js";
import { Bar } from "react-chartjs-2";
import { Pie } from "react-chartjs-2";

import {
  Chart as ChartJS,
  LineElement,
  CategoryScale, // x axis
  LinearScale, // y axis
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend,
} from "chart.js";
import { deflate } from "zlib";

ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  BarElement,
  ArcElement,
  Tooltip,
  Title,
  Legend
);

const EditBudget = styled.div``;
const Text = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  height: 100px;
  border-radius: 20px;
  padding: center;
  margin-left: 6rem;
  margin-right: 6rem;
  margin-top: 1rem;
  text-align: center;
  background-color: #f16f22;
`;
const ButtonPlush = styled.div`
  display: flex;
  justify-content: center;
  font-size: 50px;
  padding-top: 30px;
`;

const Flexmodal = styled.div`
  display: flex;
  font-weight: bold;
`;
const TableHeader = styled.div`
  margin-left: 6rem;
  margin-right: 6rem;
  /* margin-bottom: 6rem; */
  margin-top: 1rem;
  text-align: center;
`;

const Totalbudget: React.FunctionComponent = () => {
  const { state } = useLocation();
  // console.log(".................", state.item);

  const [fiscalYearId, setfiscalYearId] = useState<any>();
  const [subbudgetID, setSubbudgetID] = useState<any>([]);
  const [sub_subbudgetID, setSub_subbudgetID] = useState<any>([]);



  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [showSup, setShowSup] = useState(false);
  const handleCloseSup = () => setShowSup(false);
  const handleShowSup = () => setShowSup(true);

  const navigate = useNavigate();

  const [budgetgroup, setbudgetgroup] = useState<any>();
  const [reset, setReset] = useState<boolean>(false);

  const [budget, setBudget] = useState<any>([]);
  const [name, setName] = useState<any>([]);
  const [dataBudgetGroup, setDataBudgetGroup] = useState<any>([]);

  const [dataName, setDataName] = useState<any>([]);
  const [dataSupBudget, setDataSupBudget] = useState<any>([]);
  const [dataSubBudget, setDataSubBudget] = useState<any>([]);

  const [subbudgetName, setSubbudgetName] = useState<any>([]);
  const [subbudgetBudget, setSubbudgetBudget] = useState<any>([]);

  const [sumTotal, setSumTotal] = useState<number>(0);
  const [query, setQuery] = useState("");

  useEffect(() => {

    if (budgetgroup) {

      const nameBudgetgroupArr = [];
      const budgetBudgetgroupArr = [];
      let sumBudgetInBudgetgroup = 0;
      let remain = 0;

      let subbudgetNAME: any = []
      let subbudgetBUDGET: any = []

      let budgetgroupLength = budgetgroup.length;
      for (let count = 0; count < budgetgroupLength; count++) {
        nameBudgetgroupArr.push(budgetgroup[count].name);
        budgetBudgetgroupArr.push(budgetgroup[count].budget);

        sumBudgetInBudgetgroup += budgetgroup[count].budget;

        for (let j = 0; j < budgetgroup[count].subbudgets.length; j++) {
          subbudgetNAME.push(budgetgroup[count].subbudgets[j].name)
          subbudgetBUDGET.push(budgetgroup[count].subbudgets[j].budget)
        }

      }

      setSubbudgetName(subbudgetNAME)
      setSubbudgetBudget(subbudgetBUDGET)

      class budgetGroup {
        ชื่อ: any;
        งบประมาณ: any;
        constructor(name: any, budget: any) {
          this.ชื่อ = name;
          this.งบประมาณ = budget;
        }
      }

      let dataBudgetgroup: any = [];

      for (let i = 0; i < nameBudgetgroupArr.length; i++) {
        let data = new budgetGroup(
          nameBudgetgroupArr[i],
          budgetBudgetgroupArr[i]
        );
        dataBudgetgroup.push(data);
      }

      setSumTotal(sumBudgetInBudgetgroup);
      setName(nameBudgetgroupArr);
      setBudget(budgetBudgetgroupArr);
      setDataBudgetGroup(dataBudgetgroup);

      remain = state.item.budget - sumBudgetInBudgetgroup;

      nameBudgetgroupArr.push("คงเหลือ");
      budgetBudgetgroupArr.push(remain);


      let sumSub_sub = 0;
      let arrSumSub_sub: any = []

      //data for graph
      let arrSuperName: any = []
      let arrSuperBudget: any = []
      let arrSub_subBudget: any = []

      for (let i = 0; i < budgetgroup.length; i++) {
        arrSuperBudget.push(budgetgroup[i].budget)
        arrSuperName.push(budgetgroup[i].name)
      
        arrSumSub_sub[i] = []
        let subbudgetsCount = budgetgroup[i].subbudgets
        for (let j = 0; j < subbudgetsCount.length; j++) {
          for (let k = 0; k < sub_subbudgetID.length; k++) {
            if (subbudgetsCount[j].subbudget_id === sub_subbudgetID[k].subbudget_id) {
              sumSub_sub += sub_subbudgetID[k].budget
            }
          }
          arrSumSub_sub[i].push(sumSub_sub)
          sumSub_sub = 0
        }
      }

      let total = 0
      for (let i = 0; i < arrSumSub_sub.length; i++) {

        for (let j = 0; j < arrSumSub_sub[i].length; j++) {
          total += arrSumSub_sub[i][j]
        }
        arrSub_subBudget.push(total)
        total = 0
      }
  
      setDataName(arrSuperName)
      setDataSupBudget(arrSuperBudget)
      setDataSubBudget(arrSub_subBudget)
    }
  }, [budgetgroup]);

  useMemo(async () => {
    const getData: any = await configAxios(
      "get",
      API.getsuperbudget + state.item.budgetgroup_id
    );

    const getDataSubbudget: any = await configAxios(
      "get",
      API.getsubbudget
    );

    const getDataSub_subbudget: any = await configAxios(
      "get",
      API.getSub_subbudget
    );
    if (getDataSub_subbudget.status === 200) {
      setSub_subbudgetID(getDataSub_subbudget.data)
    }

    if (getData.status === 200 && getDataSubbudget.status === 200) {
      setbudgetgroup(getData.data);
      // setfiscalYearId(getData.data);
      setSubbudgetID(getDataSubbudget.data);
    } else {
    }
  }, [reset]);

  const labels = [
    "rgba(128,0,120)",
    "#1aaca2",
    "rgba(148,0,211)",
    "rgba(70,130,180)",
    "#617969",
    "#643ac6",
    "rgb(203,63,83)",
    "rgb(28,159,23)",
  ];
  const backgroundColorNew = [];
  for (let i = 0; i < labels.length; i++) {
    const r = Math.floor(Math.random() * 255);
    const g = Math.floor(Math.random() * 255);
    const b = Math.floor(Math.random() * 255);
    // console.log("rgba(" + r + "," + g + "," + b + ",0.2)");
    backgroundColorNew.push("rgba(" + r + "," + g + "," + b + ",0.6)");
  }
  // //Pie-Chart//
  // const data = {
  //   labels: name,
  //   datasets: [
  //     {
  //       // label: name,
  //       data: budget,
  //       // backgroundColor: getRandomColor,
  //       backgroundColor: backgroundColorNew,

  //       borderColor: ["#ffffff"],
  //       borderWidth: 1,
  //     },
  //   ],
  // };

  // const options = {};
  //

  const data = {
    labels: dataName,
    datasets: [

      {
        label: "งบอนุมัติ",
        data: dataSupBudget,
        backgroundColor: "#2227b6",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      },
      {
        label: "ยอดที่ใช้ไป",
        data: dataSubBudget,
        backgroundColor: "#990000",
        borderColor: ["#f8f8f8"],
        borderWidth: 1,
      }
    ]
  };

  const options: ChartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        // text: state.items.name
        // text: name
      }
    }

  };

  return (
    <>
      <Sidebar />
      <div>
        <Button
          onClick={() => {
            navigate(`/budget`, {});
          }}
          variant="link"
          style={{
            marginLeft: "4%",
            // height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          สรุปผลงบประมาณ /
        </Button>
        <Button
          onClick={() => {
            navigate(-1);
          }}
          variant="link"
          style={{
            // marginLeft: "4%",
            // height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          รายละเอียดปีงบประมาณ /
        </Button>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });
            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            height: "50px",
            color: "#ac2d07",
          }}
          className="btn  "
        >
          {state.item.name}
        </Button>
      </div>
      {/* <Sidebar/> */}
      <div className="m-4 " style={{ width: "15rem", textAlign: "right", }}>
        <div className="input-group rounded">
          <input
            type="search"
            className="form-control rounded"
            placeholder="ค้นหา"
            aria-label="Search"
            aria-describedby="search-addon"
            onChange={(e) => setQuery(e.target.value.toLowerCase())}
          />
          <span className="input-group-text border-1" id="search-addon">
            <i className="fas fa-search"></i>
          </span>
        </div>
      </div>
      <Card
        style={{
          backgroundColor: "#ffffff",
          borderRadius: "20px",
          color: "#F16F22",
          fontWeight: "bold",
          borderColor: " #BF360C",
          borderWidth: "3px",
        }}
        className="m-4 p-3"
      >
        <Table>
          <thead>
            <tr style={{ color: "#F16F22" }}>
              <th style={{ textAlign: "left", fontWeight: "bold" }}>
                {state.item.name}
              </th>
              <th style={{ textAlign: "right", fontWeight: "bold" }}>
                {state.item.budget.toLocaleString("en-US")} บาท
              </th>
            </tr>

            {budgetgroup != undefined && (
              <>
                {budgetgroup.filter((item: any) => item?.name.toLowerCase().includes(query)
                )
                  .map((item: any, idx: any) => (
                    <>
                      <tr style={{ color: "#054A8A" }}>
                        <th style={{ textAlign: "left", fontWeight: "bold" }}>
                          <a>
                            {idx + 1}.{"   "}
                            {item?.name}
                          </a>
                        </th>
                        <th style={{ textAlign: "right", fontWeight: "bold" }}>
                          <a>
                            {item?.budget.toLocaleString("en-US")} บาท{" "}
                          </a>
                        </th>
                      </tr>

                      {_.map(item?.subbudgets, (items, index) => {
                        return (
                          <>
                            {items?.superbudget_id === item?.superbudget_id && (
                              <tr style={{ color: "#000000" }}>
                                <th style={{ textAlign: "left", paddingLeft: "35px", fontWeight: "bold" }}>
                                  <a>
                                    {"   "}
                                    {items?.name}
                                  </a>
                                </th>
                                <th style={{ textAlign: "right", paddingRight: "35px", fontWeight: "bold" }}>
                                  <a>
                                    {items?.budget.toLocaleString("en-US")} บาท {" "}
                                  </a>
                                </th>
                              </tr>
                            )}

                            {_.map(sub_subbudgetID, (tm, dx) => {
                              return (
                                <>

                                  {tm?.subbudget_id === items?.subbudget_id && (
                                    <>
                                      < tr style={{ color: "#000000" }}>
                                        <th style={{ textAlign: "left", paddingLeft: "55px" }}>
                                          <a>
                                            {" - "}
                                            {tm?.name}
                                          </a>
                                        </th>
                                        <th style={{ textAlign: "right", paddingRight: "55px" }}>
                                          <a>
                                            {tm?.budget.toLocaleString("en-US")} บาท {" "}
                                          </a>
                                        </th>
                                      </tr>
                                    </>
                                  )
                                  }
                                </>
                              )
                            })}
                          </>
                        );
                      })}
                    </>
                  ))
                }
              </>
            )}

            <tr style={{ color: "#F16F22" }}>
              <td className="mx-2" style={{ textAlign: "left" }}>
                งบประมาณที่ใช้ไปทั้งหมด
              </td>
              <td className="mx-2" style={{ textAlign: "right" }}>
                {sumTotal.toLocaleString("en-US")} บาท
              </td>
            </tr>
            <tr style={{ color: "#F16F22" }}>
              <td className="mx-2" style={{ textAlign: "left" }}>
                คงเหลือ
              </td>
              <td className="mx-2" style={{ textAlign: "right" }}>
                {(state.item.budget - sumTotal).toLocaleString("en-US")} บาท
              </td>
            </tr>
          </thead>
        </Table>

        <p
          style={{
            display: "flex",
            justifyContent: "space-between",
            color: "#F16F22",
            fontWeight: "bold",
            border: "none",
          }}
        >
          กราฟสรุปการใช้เงินงบประมาณ
        </p>
        <Bar data={data} options={options}></Bar>
        {/* <div
          style={{
            display: "flex",
            justifyContent: "center",
            width: "80%"
          }} 
        >
           <Bar data={data} options={options}></Bar> */}
          {/* <div
            style={{
              width: "20rem",
            }}
          >
           
          </div> */}
        {/* </div> */}
        
        {/* <div
          style={{
            display: "flex",
            justifyContent: "end",
            color: "#ac2d07",
          }}
        >
          <ExportExcel excelData={dataBudgetGroup} fileName={state.item.name} />
        </div> */}
      </Card>
    </>
  );
};

export default Totalbudget;
