import { useState, useMemo, useEffect } from "react";
import "../pages/Login.css";
import { Button, Form, Card, Container, Row, Col } from "react-bootstrap";
import axios from "axios";
import Swal from 'sweetalert2'
import { API } from "../axios/swr/endpoint";
import { configAxios } from "../axios/configAxios";
import {
  MDBBtn,
  MDBContainer,
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBRow,
  MDBCol,
  MDBIcon,
  MDBInput
} from "mdb-react-ui-kit";
import styled from "styled-components";

import { url } from "inspector";
import background from "../img/login_R.png";
import bg from "../img/bgw2.png";
import { useNavigate, useLocation } from "react-router-dom";
import { baseURL } from "../axios/configBaseBRL";



function Login() {
  const [username, setUsername] = useState<string>();
  const [password, setPassword] = useState<string>();
  const navigate = useNavigate()

  const [reset, setReset] = useState<boolean>(false);
  const [admin, setAdmin] = useState<any>();
  const [mainAdmin, setMainAdmin] = useState<any>();
  const { state } = useLocation();

  // useMemo(async () => {
  //   const getData: any = await configAxios(
  //     "get",
  //     API.getadmin + state.item.admin_id
  //   );
  //   // console.log(getData);
  //   if (getData.status == 200) {
  //     setAdmin(getData.data);

  //   } else {
  //   }
  // }, [reset]);

  useEffect(() => {
    if (localStorage.getItem('token')) {
      navigate("/Budget")
    }
  }, [])


  const singIn = async () => {
    var data = {
      username: username,
      password: password,
    };


    var config = {
      method: 'post',
      url: `${baseURL}/login`,
      headers: {
        'Content-Type': 'application/json'
      },
      data: data
    };

    // console.log(config);


    try {
      const res = await axios(config);
      // console.log(res.status);
      // console.log(state.item.username);
      // console.log(res);

      if (res.status == 200) {
        Swal.fire({
          position: "center",
          icon: "success",
          title: "Login success",
          showConfirmButton: false,
          timer: 2000,
        });


        localStorage.setItem('token', res.data.token);

        if (res?.data?.fname) {
          // console.log("it's admin");
          localStorage.setItem('admin', "admin");

        } else {
          // console.log("it's main admin");
          localStorage.setItem('admin', "mainadmin");

        }
        navigate("/Budget");
      }

      // localStorage.removeItem('token');
      // navigate("");


      setUsername(""); setPassword("");
    } catch (error: any) {
      console.log(error.response.status);
      if (error.response.status == 404) {
        // console.log("123");

        Swal.fire(
          'Login Failed',
          'The username or password is incorrect',
          'warning'
        )
      }
    }
  };

  const Bg = styled.div``;
  const Text = styled.div``;
  const BoxFlex = styled.div``;
  const BoxIn = styled.div``;


  return (
    //border - กรอบข้อความ
    <MDBContainer
      className="my-5 gradient-form"
      style={{
        // backgroundColor: "#000",
        maxWidth: "960px",
        // width: "100%",
        // margin: "0 auto",
        paddingLeft: "5%",
        paddingRight: "5%",
        paddingTop: "5%",
      }}
    >
      <MDBRow
        style={{
          border: "5px  ",
          borderRadius: "20px",
          boxShadow: "1px 1px 10px 3px #615f5f",
          backgroundColor: "#ffffff",
        }}
      >
        <MDBCol style={{ justifyContent: "center", margin: "10px" }}>
          <div className="d-flex flex-column ">
            <div className="text-center">
              <h4
                // className="mt-1 "
                style={{
                  color: "#054A8A",
                  fontSize: "3rem ",
                  fontWeight: "bold",
                  paddingTop: "15%",
                }}
              >
                LOGIN
              </h4>
              <p
                style={{
                  textAlign: "center",
                  marginBottom: "10%",
                  marginTop: "3%",
                }}
              >
                Please login to your account
              </p>
            </div>

            {/*  Email - Password */}

            {/*  */}
            <MDBInput
              wrapperClass="mb-3"
              label="Username"
              id="form1"
              type="text"
              name="username"
              value={username}
              onChange={(event: any) => {
                const value = event.target.value;
                setUsername(value);
                // console.log(value);
              }}
            />
            <MDBInput
              wrapperClass="mb-5 "
              label="Password"
              id="form1"
              type="password"
              name="password"
              value={password}
              onChange={(event: any) => {
                const value = event.target.value;
                setPassword(value);
                // console.log(value);
              }}
            />
            {/*  Button */}
            <div className="text-center  mb-3 ">
              <MDBBtn onClick={async () => {
                if (username && password) {
                  await singIn()

                  // navigate("/Budget");
                }
              }}
                className="mb w-100 gradient-custom-2 "
                style={{
                  color: "#ffffff",
                  borderRadius: 5,
                  backgroundColor: username && password ? "#F16F22" : "#6b6b6b",
                  border: "none",
                }}
              >
                Sign in
              </MDBBtn>
            </div>
          </div>
        </MDBCol>

        {/*  Box Right */}
        <MDBCol col="2  " style={{ alignItems: "center" }}>
          <BoxFlex
            style={{
              backgroundImage: `url(${background})`,
              margin: "o auto",
              borderTopRightRadius: "20px",
              borderBottomRightRadius: "20px",
              marginRight: "-12px",
              marginLeft: "-12px",
              paddingBottom: "100px",
            }}
          >
            <Text className="text-white px-3 py-3 p-md-5 mx-1 md-4  ">
              <div
                className="d-flex flex-row align-items-center justify-content-center  "
                style={{ paddingTop: "10px" }}
              >
                <img
                  //  src="url(${imgs.bg_login})"
                  src="https://upload.wikimedia.org/wikipedia/th/e/e0/RMUTI_KORAT.png"
                  style={{ width: "5em" }}
                  alt="logo"
                />
              </div>
              <h4 className="mb-3 "></h4>
              <h4
                //className="mb-5 d-flex justify-content-center"
                style={{
                  display: "flex",
                  justifyContent: "center",
                  margin: "0 auto",
                  textIndent: "5px",
                  lineHeight: "2",
                  fontSize: "20px",
                  textShadow: "0px 1.5px 3px #393535",
                }}
              >
                ระบบติดตามการใช้งบประมาณ

              </h4>
              <h4
                //className="mb-5 d-flex justify-content-center"
                style={{
                  display: "flex",
                  justifyContent: "center",
                  margin: "0 auto",
                  textIndent: "5px",
                  lineHeight: "2",
                  fontSize: "20px",
                  textShadow: "0px 1.5px 3px #393535",
                }}
              >
                คณะวิศวกรรมศาสตร์

              </h4>
              {/* <p
                  className="small "
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    margin: "0 auto",
                    textIndent: "10px",
                    lineHeight: "4.5 , 1",
                    textShadow: "0px 2px 2px #393535",
                  }}
                >
                  budget tracking system Faculty of Engineering
                </p> */}
            </Text>
          </BoxFlex>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
    //   <MDBContainer className="my-5">

    //   <MDBCard>
    //     <MDBRow className='g-0'>

    //       <MDBCol md='6'>
    //         <MDBCardImage src='https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/img1.webp' alt="login form" className='rounded-start w-100'/>
    //       </MDBCol>

    //       <MDBCol md='6'>
    //         <MDBCardBody className='d-flex flex-column'>

    //           <div className='d-flex flex-row mt-2'>
    //             <MDBIcon fas icon="cubes fa-3x me-3" style={{ color: '#ff6219' }}/>
    //             <span className="h1 fw-bold mb-0">Logo</span>
    //           </div>

    //           <h5 className="fw-normal my-4 pb-3" style={{letterSpacing: '1px'}}>Sign into your account</h5>

    //             <MDBInput wrapperClass='mb-4' label='Email address' id='formControlLg' type='email' size="lg"/>
    //             <MDBInput wrapperClass='mb-4' label='Password' id='formControlLg' type='password' size="lg"/>

    //           <MDBBtn className="mb-4 px-5" color='dark' size='lg'>Login</MDBBtn>
    //           <a className="small text-muted" href="#!">Forgot password?</a>
    //           <p className="mb-5 pb-lg-2" style={{color: '#393f81'}}>Don't have an account? <a href="#!" style={{color: '#393f81'}}>Register here</a></p>

    //           <div className='d-flex flex-row justify-content-start'>
    //             <a href="#!" className="small text-muted me-1">Terms of use.</a>
    //             <a href="#!" className="small text-muted">Privacy policy</a>
    //           </div>

    //         </MDBCardBody>
    //       </MDBCol>

    //     </MDBRow>
    //   </MDBCard>

    // </MDBContainer>

  );
}

export default Login;
