// import React from "react";
import React, { useMemo, useState, useCallback, useEffect } from "react";
import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import _, { set } from "lodash";
import Table from "react-bootstrap/Table";
import { configAxios } from "../axios/configAxios";
import { API } from "../axios/swr/endpoint";
import { confiFiscal } from "../axios/configFiscal";
import { baseURL } from "../axios/configBaseBRL";
import Swal from "sweetalert2";
import Container from "react-bootstrap/Container";


const ButtonPlush = styled.div`
  display: flex;
  justify-content: start;
  padding-top: 1%;
`;
const Flexrow = styled.div`
`;
const BudgetGrop = styled.div`
`;


const BudgetMN: React.FunctionComponent = () => {
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [fiscalYear, setFiscalYear] = useState<any>();
  const [budget, setBudget] = useState<any>();
  const [nameFac, setNameFac] = useState<string>();
  const [reset, setReset] = useState<boolean>(false);

  useMemo(async () => {
    const getData: any = await confiFiscal("get", API.getfiscalyear);
    // console.log(getData);
    if (getData.status == 200) {
      setFiscalYear(getData.data);
      setNameFac(getData.data.name);
    } else {
      // console.log(getData);
    }
  }, [reset]);
  // console.log(fiscalYear);

  // console.log("fiscalYear,", fiscalYear);
  const saveButton = async () => {
    var data = {
      name: nameFac,
      budget: budget,
    };

    var config = {
      method: "post",
      url: `${baseURL}/create/fiscalyear`,
      headers: {
        "auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      data: data,
    };
    try {
      const nameFiscal = await axios(config);
      // console.log(nameFiscal);
      setShow(false);
      setFiscalYear("");
      setBudget("");
      setReset(!reset);
    } catch (error: any) {
      Swal.fire({
        position: "center",
        icon: "error",
        text: "โปรดระบุงบประมาณเป็นตัวเลขเท่านั้น",
        showConfirmButton: false,
        timer: 1500,
      });
      // console.log(error);
    }
  };

  const removeButton = async (id: number) => {
    var data = JSON.stringify({
      fiscalyear_id: id,
    });

    var config = {
      method: "post",
      maxBodyLength: Infinity,
      url: `${baseURL}/delete/fiscalyear`,
      headers: {
        "auth-token": localStorage.getItem("token"),
        "Content-Type": "application/json",
      },
      data: data,
    };

    try {
      const mss = await axios(config);
      setReset(!reset);
    } catch (error: any) {

    }
  };

  return (
    <>
      <Sidebar />

      <div>
        <Button
          onClick={() => {
            const Toast = Swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", Swal.stopTimer);
                toast.addEventListener("mouseleave", Swal.resumeTimer);
              },
            });

            Toast.fire({
              icon: "success",
              title: "คุณกำลังอยู่ในหน้านี้",
            });
          }}
          variant="link"
          style={{
            marginLeft: "4%",
            borderRadius: "12px",
            height: "50px",
            color: "#ac2d07",
            fontSize: "17px",
            alignItems: "center",
          }}
          className="btn  "
        >
          จัดการงบประมาณ
        </Button>
      </div>
      <ButtonPlush>
        <button
          onClick={handleShow}
          type="button"
          style={{
            marginLeft: "6%",
            borderRadius: "12px",
            height: "50px",
            fontSize: "15px",
            alignItems: "center",
            borderColor: " #BF360C",
            borderWidth: "3px",
            color: "#054A8A",
            fontWeight: "bold",
          }}
          className="btn btn "
        >
          เพิ่มปีงบประมาณ <i className="fa-solid fa-plus"></i>
        </button>
        <div></div>
      </ButtonPlush>

      <Container>
        <BudgetGrop>
          {_.map(fiscalYear, (item: any, idx) => {
            return (
              <>
                <div
                  className="card text-center"
                  style={{
                    backgroundColor: "#fdefe7",
                    color: "#054A8A",
                    fontWeight: "bold",
                    borderRadius: "12px",
                    margin: "10px",
                  }}
                >
                  <div className="card-body">
                    {/* <h5 className="card-title"></h5> */}
                    <Flexrow
                      className="d-flex justify-content-between  "
                      key={item?.fiscalyear_id}
                    >
                      <p className="mt-3 "> {item.name} </p>
                      <div className="d-flex flex-row">
                        <p className="mt-3 ">{item.budget.toLocaleString('en-US')} บาท</p>

                        <button
                          onClick={() => {
                            navigate(`/Editbudget/${item?.fiscalyear_id}`, {
                              state: { item: item },
                            });
                          }}
                          style={{
                            backgroundColor: "#FFB800",
                            // height: "40px",
                            // width: "80px",
                            color: "#000000",
                          }}
                          type="button"
                          className="btn mx-4 my-2"
                        >
                          <i className="fa-solid fa-pen-to-square"></i>
                        </button>

                        <button
                          onClick={() => {
                            Swal.fire({
                              title: "คุณต้องการลบ : " + item?.name,
                              text: "หากลบแล้วคุณจะเปลี่ยนกลับไม่ได้!",
                              icon: "warning",
                              showDenyButton: true,
                              confirmButtonText: "ยืนยัน",
                              denyButtonText: "ยกเลิก",
                              confirmButtonColor: "#0f754e",
                            }).then((result) => {
                              if (result.isConfirmed) {
                                Swal.fire({
                                  position: "center",
                                  icon: "success",
                                  title: "ข้อมูลของคุณถูกลบสำเสร็จ",
                                  showConfirmButton: false,
                                  timer: 1500,
                                });
                                {
                                  removeButton(item?.fiscalyear_id);
                                }
                              } else if (result.isDenied) {
                                // Swal.fire("Changes are not saved", "", "info");
                              }
                            });
                          }}
                          style={{
                            backgroundColor: "#ff3700",
                            // height: "40px",
                            // width: "80px",
                            color: "#000000",
                            // marginRight: "90px",
                          }}
                          type="button"
                          className="btn my-2"
                        >
                          <i className="fa-solid fa-trash"></i>
                        </button>
                      </div>
                    </Flexrow>
                  </div>
                </div>
              </>
            );
          })}
        </BudgetGrop>
      </Container>
      {/* Modal เพิ่มปีงบประมาณ */}
      <Modal
        show={show}
        onHide={handleClose}
        // size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
          <Modal.Header closeButton></Modal.Header>
          <Modal.Title
            style={{
              alignItems: "center",
              color: "#484848",
              textAlign: "center",
              padding: "10px 10px 30px 10px",
              fontWeight: "bold",
            }}
          >
            ปีงบประมาณ
          </Modal.Title>
          <div className="d-flex justify-content-center">
            <Form>
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Control
                  style={{
                    backgroundColor: "#ffffff",
                    borderColor: " #F16F22",
                    borderWidth: "2px",
                    borderRadius: "12px",
                    color: "#000000",
                    fontWeight: "bold",
                  }}
                  type="text"
                  placeholder="ระบุปีงบประมาณ"
                  name="fiscalYear"
                  value={nameFac}
                  onChange={(event: any) => {
                    const value = event.target.value;
                    setNameFac(value);
                    // console.log(value);
                  }}
                />
                <Form.Control
                  style={{
                    backgroundColor: "#ffffff",
                    borderColor: " #F16F22",
                    borderWidth: "2px",
                    borderRadius: "12px",
                    color: "#000000",
                    fontWeight: "bold",
                    margin: "1.5rem auto",
                  }}
                  type="text"
                  placeholder="เงินงบประมาณ"
                  name="total"
                  value={budget}
                  pattern="^[0-9]*$"
                  onChange={(event: any) => {
                    const value = event.target.value;
                    setBudget(value);
                    // console.log(value);
                  }}
                  // autoFocus
                />
              </Form.Group>
            </Form>
          </div>
        </Modal.Body>

        <Button
          style={{
            display: "flex",
            justifyContent: "center",
            color: "#ffffff",
            backgroundColor: fiscalYear && budget ? "#278A43" : "#6b6b6b",
            border: "none",
          }}
          onClick={() => {
            if (nameFac && budget) {
              Swal.fire({
                title: "ยืนยันการบันทึก",
                text: nameFac + " งบประมาณ " + budget.toLocaleString("en-US") + " บาท",
                // text: "ผู้รับผิดชอบ " + noteBudget,
                showDenyButton: true,
                confirmButtonText: "ยืนยัน",
                denyButtonText: "ยกเลิก",
                confirmButtonColor: "#0f754e",
              }).then((result) => {
                if (result.isConfirmed) {
                  Swal.fire({
                    position: "center",
                    icon: "success",
                    title: "บันทึกสำเสร็จ",
                    showConfirmButton: false,
                    timer: 1500,
                  });
                  {
                    saveButton();
                  }
                } else if (result.isDenied) {

                }
              });
              
            }
          }}
        >
          บันทึกข้อมูล
        </Button>
      </Modal>
      {/* Modal เพิ่มปีงบประมาณ */}
    </>
  );
};

export default BudgetMN;
